@use "global/var" as v;
@use "global/mixins" as m;

.big-circle {
  width: 50px;
  height: 50px;
  border: 4px solid cyan;
  border-radius: 50%;
  position: relative;
  left: 80%;
  transition: transform 0.1s linear; /* suaviza el bamboleo */
}

.small-circle {
  width: 20px;
  height: 20px;
  border: 4px solid tomato;
  border-radius: 50%;
  position: absolute;
  top: 25%;
  left: 25%;
  transform: translate(-50%, -50%);
}

.Lenguages {
  display: flex;
  flex-direction: column;

  &:hover {
    cursor: pointer;
  }
  &.lenguageActiveEN {
    flex-direction: column-reverse;
  }
  .languageES {
    text-align: center;
    opacity: 0;

    &.lenguageSelected {
      opacity: 1;
      transition: opacity 0.3s ease-in-out;
    }
  }
  .languageEN {
    text-align: center;
    opacity: 0;
    &.lenguageSelected {
      opacity: 1;
      transition: opacity 0.3s ease-in-out;
    }
  }
}
