@use "global/var" as v;
@use "global/mixins" as m;

.main {
  padding: 4rem;
}
.main__container {
  @include m.desktop {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .main__container__left {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    .main__text {
      font-size: 2.5rem;
    }
    .main__container__left__img {
      border-radius: 50%;
      width: 20rem;
      height: 20rem;
    }
    .main__container__left__button {
      .main__container__left__button__text {
        &:hover {
          opacity: 0.9;
        }
        display: flex;
        align-items: center;
        justify-content: space-around;
        padding: 0.5rem 0.5rem;
        background-color: #fd313b;
        width: 50%;
        border-radius: 10px;
        height: 5rem;
        transition: background-color 0.5s ease;

        p {
          color: v.$White;

          font-size: 2rem;
        }
        .main__container__left__button__Img {
          background-color: #f8686f;
          padding: 0.2rem;
          border-radius: 10px;
          img {
            width: 4rem;
            height: 4rem;
            filter: invert(100%);
          }
        }
      }
    }
  }

  .main__container__right {
    .main__container__right__skills {
    }
    .main__container__right__button {
      &:hover {
        opacity: 0.9;
      }
      width: 15rem;
      border-bottom: 1px solid #f8686f;
      a {
        .main__container__right__button__text {
          display: flex;
          gap: 0.4rem;

          p {
            text-transform: uppercase;
            margin: 0;
            padding: 0;
            color: #f8686f;
          }

          img {
            width: 2rem;
            height: 2rem;
            filter: brightness(0) saturate(100%) invert(55%) sepia(50%)
              saturate(504%) hue-rotate(312deg) brightness(97%) contrast(94%);
          }
        }
      }
    }
  }
}
