/* Global */
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  background-color: #0f0f0f;
  color: #fff;
  margin: 0; /* Elimina el margen predeterminado */
  font-size: 1.6rem;
  line-height: 1.5;
}

h1 {
  font-size: 3rem;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

img {
  display: block;
  max-width: 95%;
}

.content {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.meta-dato {
  color: #4caf50;
}

.highlight {
  color: #ff004c;
}

.gradient {
  opacity: 0.5;
}

.centrar-texto {
  text-align: center;
}

/** Media Queries **/
/* Document
 * ========================================================================== */
/**
 * 1. Correct the line height in all browsers.
 * 2. Prevent adjustments of font size after orientation changes in
 *    IE on Windows Phone and in iOS.
 */
html {
  line-height: 1.15; /* 1 */
  -ms-text-size-adjust: 100%; /* 2 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections
   * ========================================================================== */
/**
   * Correct the font size and margin on `h1` elements within `section` and
   * `article` contexts in Chrome, Edge, Firefox, and Safari.
   */
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content
   * ========================================================================== */
/**
   * Remove the margin on nested lists in Chrome, Edge, IE, and Safari.
   */
dl dl,
dl ol,
dl ul,
ol dl,
ul dl {
  margin: 0;
}

/**
   * Remove the margin on nested lists in Edge 18- and IE.
   */
ol ol,
ol ul,
ul ol,
ul ul {
  margin: 0;
}

/**
   * 1. Add the correct box sizing in Firefox.
   * 2. Correct the inheritance of border color in Firefox.
   * 3. Show the overflow in Edge 18- and IE.
   */
hr {
  box-sizing: content-box; /* 1 */
  color: inherit; /* 2 */
  height: 0; /* 1 */
  overflow: visible; /* 3 */
}

/**
   * Add the correct display in IE.
   */
main {
  display: block;
}

/**
   * 1. Correct the inheritance and scaling of font size in all browsers.
   * 2. Correct the odd `em` font sizing in all browsers.
   */
pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/* Text-level semantics
   * ========================================================================== */
/**
   * Remove the gray background on active links in IE 10.
   */
a {
  background-color: transparent;
}

/**
   * Add the correct text decoration in Edge 18-, IE, and Safari.
   */
abbr[title] {
  text-decoration: underline;
  text-decoration: underline dotted;
}

/**
   * Add the correct font weight in Chrome, Edge, and Safari.
   */
b,
strong {
  font-weight: bolder;
}

/**
   * 1. Correct the inheritance and scaling of font size in all browsers.
   * 2. Correct the odd `em` font sizing in all browsers.
   */
code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/**
   * Add the correct font size in all browsers.
   */
small {
  font-size: 80%;
}

/* Embedded content
   * ========================================================================== */
/**
   * Add the correct display in IE 9-.
   */
audio,
video {
  display: inline-block;
}

/**
   * Add the correct display in iOS 4-7.
   */
audio:not([controls]) {
  display: none;
  height: 0;
}

/**
   * Remove the border on images within links in IE 10-.
   */
img {
  border-style: none;
}

/**
   * Hide the overflow in IE.
   */
svg:not(:root) {
  overflow: hidden;
}

/* Tabular data
   * ========================================================================== */
/**
   * 1. Correct table border color inheritance in all Chrome, Edge, and Safari.
   * 2. Remove text indentation from table contents in Chrome, Edge, and Safari.
   */
table {
  border-color: inherit; /* 1 */
  text-indent: 0; /* 2 */
}

/* Forms
   * ========================================================================== */
/**
   * Remove the margin on controls in Safari.
   */
button,
input,
select {
  margin: 0;
}

/**
   * 1. Show the overflow in IE.
   * 2. Remove the inheritance of text transform in Edge 18-, Firefox, and IE.
   */
button {
  overflow: visible; /* 1 */
  text-transform: none; /* 2 */
}

/**
   * Correct the inability to style buttons in iOS and Safari.
   */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

/**
   * Correct the padding in Firefox.
   */
fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
   * Show the overflow in Edge 18- and IE.
   */
input {
  overflow: visible;
}

/**
   * 1. Correct the text wrapping in Edge 18- and IE.
   * 2. Correct the color inheritance from `fieldset` elements in IE.
   */
legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  white-space: normal; /* 1 */
}

/**
   * 1. Add the correct display in Edge 18- and IE.
   * 2. Add the correct vertical alignment in Chrome, Edge, and Firefox.
   */
progress {
  display: inline-block; /* 1 */
  vertical-align: baseline; /* 2 */
}

/**
   * Remove the inheritance of text transform in Firefox.
   */
select {
  text-transform: none;
}

/**
   * 1. Remove the margin in Firefox and Safari.
   * 2. Remove the default vertical scrollbar in IE.
   */
textarea {
  margin: 0; /* 1 */
  overflow: auto; /* 2 */
}

/**
   * 1. Add the correct box sizing in IE 10-.
   * 2. Remove the padding in IE 10-.
   */
[type=checkbox],
[type=radio] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
}

/**
   * 1. Correct the odd appearance in Chrome, Edge, and Safari.
   * 2. Correct the outline style in Safari.
   */
[type=search] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/**
   * Correct the cursor style of increment and decrement buttons in Safari.
   */
::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
  height: auto;
}

/**
   * Correct the text style of placeholders in Chrome, Edge, and Safari.
   */
::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54;
}

/**
   * Remove the inner padding in Chrome, Edge, and Safari on macOS.
   */
::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
   * 1. Correct the inability to style upload buttons in iOS and Safari.
   * 2. Change font properties to `inherit` in Safari.
   */
::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

/**
   * Remove the inner border and padding of focus outlines in Firefox.
   */
::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
   * Restore the focus outline styles unset by the previous rule in Firefox.
   */
:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
   * Remove the additional :invalid styles in Firefox.
   */
:-moz-ui-invalid {
  box-shadow: none;
}

/* Interactive
   * ========================================================================== */
/*
   * Add the correct display in Edge 18- and IE.
   */
details {
  display: block;
}

/*
   * Add the correct styles in Edge 18-, IE, and Safari.
   */
dialog {
  background-color: white;
  border: solid;
  color: black;
  display: block;
  height: -moz-fit-content;
  height: -webkit-fit-content;
  height: fit-content;
  left: 0;
  margin: auto;
  padding: 1em;
  position: absolute;
  right: 0;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
}

dialog:not([open]) {
  display: none;
}

/*
   * Add the correct display in all browsers.
   */
summary {
  display: list-item;
}

/* Scripting
   * ========================================================================== */
/**
   * Add the correct display in IE 9-.
   */
canvas {
  display: inline-block;
}

/**
   * Add the correct display in IE.
   */
template {
  display: none;
}

/* User interaction
   * ========================================================================== */
/**
   * Add the correct display in IE 10-.
   */
[hidden] {
  display: none;
}

/* Layout */
.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem 10rem;
}
@media (min-width: 1024px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header .header__texto {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
.header .header__texto a h1 {
  color: #fff;
}
@media (min-width: 1024px) {
  .header .header__texto {
    gap: 10rem;
    flex-direction: row;
  }
}
.header .MenuSvg {
  width: 4rem;
  height: 4rem;
  filter: invert(100%);
}
.header .MenuSvg:hover {
  cursor: pointer;
  filter: invert(70%);
}
.header .header__nav {
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease-in-out;
}
.header .header__nav a {
  color: #fff;
  margin-top: 1rem;
}
.header .header__nav a:hover {
  color: #ff004c;
}
.header .header__nav.active {
  opacity: 1;
  pointer-events: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .header .header__nav.active {
    flex-direction: row;
    gap: 4rem;
  }
}

.main {
  padding: 4rem;
}

@media (min-width: 1024px) {
  .main__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.main__container .main__container__left {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.main__container .main__container__left .main__text {
  font-size: 2.5rem;
}
.main__container .main__container__left .main__container__left__img {
  border-radius: 50%;
  width: 20rem;
  height: 20rem;
}
.main__container .main__container__left .main__container__left__button .main__container__left__button__text:hover {
  opacity: 0.9;
}
.main__container .main__container__left .main__container__left__button .main__container__left__button__text {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0.5rem 0.5rem;
  background-color: #fd313b;
  width: 50%;
  border-radius: 10px;
  height: 5rem;
  transition: background-color 0.5s ease;
}
.main__container .main__container__left .main__container__left__button .main__container__left__button__text p {
  color: #fff;
  font-size: 2rem;
}
.main__container .main__container__left .main__container__left__button .main__container__left__button__text .main__container__left__button__Img {
  background-color: #f8686f;
  padding: 0.2rem;
  border-radius: 10px;
}
.main__container .main__container__left .main__container__left__button .main__container__left__button__text .main__container__left__button__Img img {
  width: 4rem;
  height: 4rem;
  filter: invert(100%);
}
.main__container .main__container__right .main__container__right__button:hover {
  opacity: 0.9;
}
.main__container .main__container__right .main__container__right__button {
  width: 15rem;
  border-bottom: 1px solid #f8686f;
}
.main__container .main__container__right .main__container__right__button a .main__container__right__button__text {
  display: flex;
  gap: 0.4rem;
}
.main__container .main__container__right .main__container__right__button a .main__container__right__button__text p {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  color: #f8686f;
}
.main__container .main__container__right .main__container__right__button a .main__container__right__button__text img {
  width: 2rem;
  height: 2rem;
  filter: brightness(0) saturate(100%) invert(55%) sepia(50%) saturate(504%) hue-rotate(312deg) brightness(97%) contrast(94%);
}

.big-circle {
  width: 50px;
  height: 50px;
  border: 4px solid cyan;
  border-radius: 50%;
  position: relative;
  left: 80%;
  transition: transform 0.1s linear; /* suaviza el bamboleo */
}

.small-circle {
  width: 20px;
  height: 20px;
  border: 4px solid tomato;
  border-radius: 50%;
  position: absolute;
  top: 25%;
  left: 25%;
  transform: translate(-50%, -50%);
}

.Lenguages {
  display: flex;
  flex-direction: column;
}
.Lenguages:hover {
  cursor: pointer;
}
.Lenguages.lenguageActiveEN {
  flex-direction: column-reverse;
}
.Lenguages .languageES {
  text-align: center;
  opacity: 0;
}
.Lenguages .languageES.lenguageSelected {
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}
.Lenguages .languageEN {
  text-align: center;
  opacity: 0;
}
.Lenguages .languageEN.lenguageSelected {
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

.footer a {
  color: #fff;
}
.footer {
  padding: 4rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.footer .footer__social {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.footer .footer__social a img {
  width: 3rem;
  height: 3rem;
  filter: invert(100%);
  transition: transform 0.3s ease-in-out;
}
.footer .footer__social a img:hover {
  transform: scale(1.1);
}
.footer .footer__social .footer__social__github {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.footer .footer__social .footer__social__github .footer__social__github__location {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.footer .footer__social .footer__social__github .footer__social__github__location img {
  width: 3rem;
  height: 3rem;
  filter: invert(100%);
}
.footer .footer__contact {
  display: flex;
  justify-content: center;
  flex-direction: row-reverse;
  gap: 2rem;
}
.footer .footer__contact .footer__contact__circle img {
  position: absolute;
  filter: invert(100%);
  width: 4rem;
  height: 4rem;
}
.footer .footer__contact .footer__contact__circle .circleContact {
  position: relative;
  width: 1rem;
  height: 1rem;
  border: 4px solid #00f80c;
  border-radius: 50%;
  left: 28px;
  top: 11px;
}

.Proyectos {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .Proyectos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .Proyectos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.proyectos {
  padding: 5rem 2rem;
  text-align: center;
}
.proyectos .proyectos__title {
  font-size: 3rem;
  margin-bottom: 1rem;
  margin-top: 6rem;
}
.proyectos .proyectos__title .resaltar {
  color: #00b4ff;
}
.proyectos .proyectos__subtitle {
  max-width: 650px;
  margin: 0 auto 3rem;
  color: #ccc;
  font-size: 1.1rem;
}
.proyectos .proyectos__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .proyectos .proyectos__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .proyectos .proyectos__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
.proyectos .proyecto {
  background: #111;
  border-radius: 12px;
  overflow: hidden;
  border: 1px solid #222;
  transition: transform 0.25s ease, box-shadow 0.25s ease;
}
.proyectos .proyecto:hover {
  transform: translateY(-4px);
  box-shadow: 0 0 20px rgba(0, 180, 255, 0.2);
}
.proyectos .proyecto:hover .proyecto__content h3 {
  color: #0094ff;
}
.proyectos .proyecto__img {
  height: 180px;
  overflow: hidden;
}
.proyectos .proyecto__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.proyectos .proyecto__content {
  padding: 1.4rem;
  text-align: left;
}
.proyectos .proyecto__content h3 {
  margin-bottom: 0.6rem;
  font-size: 1.9rem;
}
.proyectos .proyecto__content p {
  color: #bbb;
  font-size: 1.2rem;
  margin-bottom: 1rem;
}
.proyectos .proyecto__tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.4rem;
  margin-bottom: 1rem;
}
.proyectos .proyecto__tags span {
  font-size: 1rem;
  padding: 4px 10px;
  border-radius: 50px;
  background: rgba(0, 180, 255, 0.1);
  border: 1px solid rgba(0, 180, 255, 0.3);
  color: #00b4ff;
}
.proyectos .proyecto__btn {
  display: inline-block;
  margin-top: 0.5rem;
  color: #00b4ff;
  border: 1px solid #00b4ff;
  padding: 8px 14px;
  border-radius: 6px;
  font-size: 0.9rem;
  transition: 0.3s;
  text-decoration: none;
}
.proyectos .proyecto__btn:hover {
  background: #00b4ff;
  color: #000;
}