@use "global/var" as v;

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: v.$fuentePrincipal;
  background-color: v.$Dark;
  color: v.$White;

  margin: 0; /* Elimina el margen predeterminado */
  font-size: 1.6rem;
  line-height: 1.5;
}
h1 {
  font-size: 3rem;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}
img {
  display: block;
  max-width: 95%;
}

.content {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.meta-dato {
  color: v.$Green;
}

.highlight {
  color: #ff004c;
}

.gradient {
  opacity: 0.5;
}

.centrar-texto {
  text-align: center;
}
