@use "global/var" as v;
@use "global/mixins" as m;
.Proyectos {
  display: flex;
  flex-direction: column;

  @include m.tablet {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
  @include m.desktop {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.proyectos {
  padding: 5rem 2rem;
  text-align: center;

  .proyectos__title {
    font-size: 3rem;
    margin-bottom: 1rem;
    margin-top: 6rem;
    .resaltar {
      color: #00b4ff;
    }
  }

  .proyectos__subtitle {
    max-width: 650px;
    margin: 0 auto 3rem;
    color: #ccc;
    font-size: 1.1rem;
  }

  .proyectos__grid {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
    margin-top: 2rem;

    @include m.tablet {
      grid-template-columns: repeat(2, 1fr);
    }

    @include m.desktop {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  .proyecto {
    background: #111;
    border-radius: 12px;
    overflow: hidden;
    border: 1px solid #222;
    transition: transform 0.25s ease, box-shadow 0.25s ease;

    &:hover {
      transform: translateY(-4px);
      box-shadow: 0 0 20px rgba(0, 180, 255, 0.2);

      .proyecto__content {
        h3 {
          color: v.$blue;
        }
      }
    }

    &__img {
      height: 180px;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        display: block;
      }
    }

    &__content {
      padding: 1.4rem;
      text-align: left;

      h3 {
        margin-bottom: 0.6rem;
        font-size: 1.9rem;
      }

      p {
        color: #bbb;
        font-size: 1.2rem;
        margin-bottom: 1rem;
      }
    }

    &__tags {
      display: flex;
      flex-wrap: wrap;
      gap: 0.4rem;
      margin-bottom: 1rem;

      span {
        font-size: 1rem;
        padding: 4px 10px;
        border-radius: 50px;
        background: rgba(0, 180, 255, 0.1);
        border: 1px solid rgba(0, 180, 255, 0.3);
        color: #00b4ff;
      }
    }

    &__btn {
      display: inline-block;
      margin-top: 0.5rem;
      color: #00b4ff;
      border: 1px solid #00b4ff;
      padding: 8px 14px;
      border-radius: 6px;
      font-size: 0.9rem;
      transition: 0.3s;
      text-decoration: none;
      &:hover {
        background: #00b4ff;
        color: #000;
      }
    }
  }
}
