@use "global/var" as v;
@use "global/mixins" as m;

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem 10rem;
  @include m.desktop {
    flex-direction: row;
    justify-content: space-between;
  }
  .header__texto {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    a {
      h1 {
        color: v.$White;
      }
    }
    @include m.desktop {
      gap: 10rem;
      flex-direction: row;
    }
  }
  .MenuSvg {
    width: 4rem;
    height: 4rem;
    filter: invert(100%);
    &:hover {
      cursor: pointer;
      filter: invert(70%);
    }
  }
  .header__nav {
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s ease-in-out;
    a {
      color: v.$White;
      margin-top: 1rem;
      &:hover {
        color: #ff004c;
      }
    }
    &.active {
      opacity: 1;
      pointer-events: auto;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 2rem;

      @include m.desktop {
        flex-direction: row;
        gap: 4rem;
      }
    }
  }
}
