@use "global/var" as v;
@use "global/mixins" as m;

.footer {
  a {
    color: v.$White;
  }
  padding: 4rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  .footer__social {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    a {
      img {
        width: 3rem;
        height: 3rem;
        filter: invert(100%);
        transition: transform 0.3s ease-in-out;
        &:hover {
          transform: scale(1.1);
        }
      }
    }
    .footer__social__github {
      display: flex;

      align-items: center;
      gap: 2rem;
      .footer__social__github__location {
        display: flex;
        align-items: center;
        gap: 0.5rem;
        img {
          width: 3rem;
          height: 3rem;
          filter: invert(100%);
        }
      }
    }
  }

  .footer__contact {
    display: flex;
    justify-content: center;
    flex-direction: row-reverse;
    gap: 2rem;
    .footer__contact__circle {
      img {
        position: absolute;
        filter: invert(100%);
        width: 4rem;
        height: 4rem;
      }
      .circleContact {
        position: relative;
        width: 1rem;
        height: 1rem;
        border: 4px solid #00f80c;
        border-radius: 50%;
        left: 28px;
        top: 11px;
      }
    }
  }
}
